import {
	ajax
} from '../../common/js/util.js'
import {
	Toast
} from 'vant'
export default {
	data() {
		return {
			Color: '#333333',
			value: '',
			backColor: '#E73C1D',
			city1: '',
			city2: '',
			day: '',
			price: {
				type: true,
				data: []
			},
			storageDate: [],
			swiperData: {},
			slideLeft: 0,
			isDoRefresh: false,
			item1: 1,
			sendDate: '',
			singleStar: '',
			singleEnd: '',
			checked1: false,
			listData: [],
			show: false,
			showAir: false
		}
	},
	computed: {
		date() {
			let _dateData = new Date(),
				_date = `${_dateData.getFullYear()}-${_dateData.getMonth() + 1}-${_dateData.getDate()}`
			return _date
		},
		date1() {
			return this.$store.state.date
		},
		day1() {
			return this.$store.state.day
		},
		initDate() {
			return this.$store.state.initDate
		}
	},
	components: {
		Toast
	},
	onLoad(option) {
		//刷新保留用户uid
		if (this.getRequest('uid')) {
			var uids = this.getRequest('uid')
			this.$store.commit('changeUid', uids)
		}
		this.singleStart = this.$store.state.startCity
		this.singleEnd = this.$store.state.endCity
		let city1 = this.$store.state.city1
		let city2 = this.$store.state.city2
		this.city1 = city1
		this.city2 = city2
		this.init()
	},
	onShow: function(e) {
		this.showAir = false
		if (this.initDate) {
			let week = new Date(this.initDate).getDay()
			week = this.getDays(week)
			let dateArr = this.initDate.split('-')
			dateArr[1] = parseInt(dateArr[1]) > 9 ? dateArr[1] : '0' + parseInt(dateArr[1])
			dateArr[2] = parseInt(dateArr[2]) > 9 ? dateArr[2] : '0' + parseInt(dateArr[2])
			this.sendDate = dateArr[0] + '/' + dateArr[1] + '/' + dateArr[2] + ' ' + week
			if (this.initDate != '' && dateArr.length == 3) {
				this.Preprocessing(dateArr)
				this.init()
			}
		} else {
			let dateArr = this.$store.state.priceDate.split('-')
			let week = new Date(this.$store.state.priceDate).getDay()
			week = this.getDays(week)
			dateArr[1] = parseInt(dateArr[1]) > 9 ? dateArr[1] : '0' + parseInt(dateArr[1])
			dateArr[2] = parseInt(dateArr[2]) > 9 ? dateArr[2] : '0' + parseInt(dateArr[2])
			this.sendDate = dateArr[0] + '/' + dateArr[1] + '/' + dateArr[2] + ' ' + week
			if (this.$store.state.priceDate != '' && dateArr.length == 3) {
				this.Preprocessing(dateArr)
			}
		}
	},
	methods: {
		getRequest(variable) {
			var query = window.location.search.substring(1);
			var vars = query.split("&");
			for (var i = 0; i < vars.length; i++) {
				var pair = vars[i].split("=");
				if (pair[0] == variable) {
					return pair[1];
				}
			}
			return (false);
		},
		init() {
			let self = this
			this.show = true
			let datas = {
				DepartCityCode: this.singleStart,
				ArriveCityCode: this.singleEnd,
				IsChild: this.$store.state.checked,
				DepartDate: this.$store.state.singleStart
			}
			let data = {
				url: '/api/gzh/flightQuery',
				data: datas,
				success: function(res) {
					self.listData = []
					if (res.data.result == 0) {
						self.show = false
						self.listData = res.data.Data
					} else {
						self.show = false
						self.showAir = true
					}
				},
				fail: function(res) {
					console.log(res)
				}
			}
			ajax(data)
		},
		changeNav(type) {
			if (this.item1 !== type) {
				this.item1 = type
			}

			if (type == 0) {
				this.listData.sort(this.CompareDate1)
			}

			if (type == 1) {
				this.listData.sort(this.CompareDate)
			}
		},
		CompareDate(t1, t2) {
			var date = new Date();
			var a = t1.DepartTime.split(":")
			var b = t2.DepartTime.split(":")
			return date.setHours(a[0], a[1]) - date.setHours(b[0], b[1])
		},
		CompareDate1(t1, t2) {
			var a = t1.Cabins[0].Fare
			var b = t2.Cabins[0].Fare
			return parseInt(a) - parseInt(b)
		},
		goDetail(k) {
			uni.navigateTo({
				url: '../singleAirDetail/singleAirDetail?date=' + this.sendDate + '&index=' + k + '&list=' + JSON.stringify(this
					.listData)
			})
		},
		back() {
			this.$store.commit('changeInit', '')
			uni.navigateBack({
				delta: 1
			})
		},
		dateFtt(fmt, date) {
			var o = {
				"M+": date.getMonth() + 1, //月份   
				"d+": date.getDate(), //日   
				"h+": date.getHours(), //小时   
				"m+": date.getMinutes(), //分   
				"s+": date.getSeconds(), //秒   
				"q+": Math.floor((date.getMonth() + 3) / 3), //季度   
				"S": date.getMilliseconds() //毫秒   
			}
			if (/(y+)/.test(fmt))
				fmt = fmt.replace(RegExp.$1, (date.getFullYear() + "").substr(4 - RegExp.$1.length))
			for (var k in o)
				if (new RegExp("(" + k + ")").test(fmt))
					fmt = fmt.replace(RegExp.$1, (RegExp.$1.length == 1) ? (o[k]) : (("00" + o[k]).substr(("" + o[k]).length)))
			return fmt
		},
		getDays(day) {
			switch (day) {
				case 0:
					day = "周日"
					break
				case 1:
					day = "周一"
					break
				case 2:
					day = "周二"
					break
				case 3:
					day = "周三"
					break
				case 4:
					day = "周四"
					break
				case 5:
					day = "周五"
					break
				case 6:
					day = "周六"
			}
			return day
		},
		// 判断当前是 安卓还是ios ，传入不容的日期格式
		judgeDate(dateData) {
			if (typeof dateData !== 'object') {
				dateData = dateData.replace(/-/g, '/')
			}
			return dateData
		},
		getDay(dateData) {
			dateData = this.judgeDate(dateData)
			// 获取年，月，日，星期
			let _date = new Date(dateData),
				year = _date.getFullYear(),
				month = _date.getMonth() + 1,
				date = _date.getDate(),
				day = _date.getDay()
			return new Promise((resolve, reject) => {
				// 获取本月
				let thisDayArr = [],
					thisDaylength = new Date(year, month - 1, 0).getDate()
				for (let i = 1; i <= new Date(year, month, 0).getDate(); i++) {
					let days = `${year}-${month}-${i}`
					days = this.judgeDate(days)
					days = this.getDays(new Date(days).getDay())
					month = parseInt(month) <= 9 ? '0' + parseInt(month) : parseInt(month)
					i = i <= 9 ? '0' + i : i
					thisDayArr.push({
						day: `${month}-${i}`,
						date: i,
						Choice: false,
						week: days,
						price: this.price.data[i - 1],
						priceType: this.price.type,
						disable: false
					})
				}
				resolve({
					dateDay: thisDayArr,
					year: year,
					month: month
				})
			})
		},
		Preprocessing(arr) {
			let self = this
			let swiperData = {}
			let _date = new Date(`${arr[0]}-${arr[1]}-${arr[2]}`),
				year = _date.getFullYear(),
				month = _date.getMonth() + 1
			this.getDay(`${arr[0]}-${arr[1]}-${arr[2]}`).then((val) => {
				swiperData = val
				arr[1] = parseInt(arr[1]) > 9 ? parseInt(arr[1]) : '0' + parseInt(arr[1])
				arr[2] = parseInt(arr[2]) > 9 ? parseInt(arr[2]) : '0' + parseInt(arr[2])
				let str = arr[1] + '-' + arr[2]
				this.$set(this, 'swiperData', swiperData)
				let __beForeDeta = new Date()
				let dDate = `${__beForeDeta.getFullYear()}-${__beForeDeta.getMonth() + 1}-${__beForeDeta.getDate()}`
				dDate = dDate.split('-')
				console.log(dDate)
				this.swiperData.dateDay = this.swiperData.dateDay.map((val, index) => {
					if (dDate[0] > year) {
						val.disable = true
					} else if (dDate[1] > month && dDate[0] >= year) {
						val.disable = true
					} else if (dDate[0] >= year && dDate[2] > parseInt(val.date) && dDate[1] >= parseInt(month)) {
						val.disable = true
					}
					return val
				})
				for (let i = 0; i < this.swiperData.dateDay.length; i++) {
					if (str == this.swiperData.dateDay[i].day) {
						this.swiperData.dateDay[i].Choice = true
						this.slideLeft = 50 * i
					}
				}
				console.log(this.swiperData)
			})
		},
		changeDay(k) {
			console.log(this.swiperData)
			if (this.swiperData.dateDay[k].disable) {
				return
			}

			this.swiperData.dateDay.forEach((val, inde) => {
				val.Choice = false
			})
			this.swiperData.dateDay[k].Choice = true
			this.sendDate = this.swiperData.year + '/' + this.swiperData.month + '/' + this.swiperData.dateDay[k].date + ' ' +
				this.swiperData.dateDay[k].week
			this.$store.commit('changeSingleStart', this.swiperData.year + '/' + this.swiperData.month + '/' + this.swiperData.dateDay[
				k].date)
			this.$store.commit('changeDate', this.swiperData.month + '-' + this.swiperData.dateDay[k].date)
			this.$store.commit('changeInit', this.swiperData.year + '-' + this.swiperData.month + '-' + this.swiperData.dateDay[
				k].date)
			this.$store.commit('changePrice', this.swiperData.year + '-' + this.swiperData.month + '-' + this.swiperData.dateDay[
				k].date)
			this.$store.commit('changeDay', this.swiperData.dateDay[k].week)
			this.init()
		},
		choiceWalk() {
			this.$store.commit('changeInit', '')
			uni.navigateTo({
				url: '../selectTime/selectTime?type=3'
			})
		}
	}
}
